				hotHeight:0,
				tabbar: [],
				tabIndex: 0,
				dataArray:[],
				heightArray:[]
			}
		},
		onReady() {
			const res = uni.getSystemInfoSync();
			if(parseInt(res.safeArea.top) > 44){
				this.system_top = parseInt(res.safeArea.top)
			}
			this.scrollHeight = res.windowHeight - 5 - this.system_top - 40
			this.hotHeight = res.windowHeight - 150 - 75 - 40
			this.tabbar = [this.i18n.toher.remen, '采耳', '采耳', '采耳', '采耳', '采耳', '采耳', '采耳', '采耳', '采耳']
			this.dataArray = ['采耳', '采耳', '采耳', '采耳', '采耳', '采耳', '采耳', '采耳', '采耳']
			this.getSectionHeight()
			
		},
		onLoad(option) {
			this.storeID = option.id
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		methods: {
			doback(){
				uni.navigateBack({
					
				})
			},
			tabbarClick(index){
				this.tabIndex = index
			},
			animationfinish(e) {
				this.tabIndex = e.detail.current
			},
			doRank(){
				uni.navigateTo({
					url:'../../../pages/rank/rank?type=meishi'
				})
			},
			more(){
				uni.navigateTo({
					url:'./indexMore'
				})
			},
			getSectionHeight(){
				console.log(213)
				var hh = 0;
				for (let i = 0; i < this.dataArray.length; i++) {
					hh = hh + (50 + 130*2);
					this.heightArray.push(hh)
				}
				console.log(this.heightArray)
			},
			scrollChange(event){
				
				for (let i = 0; i < this.heightArray.length; i++) {
					if(this.heightArray[i] > event.detail.scrollTop){
						this.tabIndex = i+1
						break
					}
				}
				console.log(this.tabIndex)
			},
			onChange(){
				
			},
			gotocell(item){
				uni.navigateTo({
					url:'../fuwuxiangqing/index'
				})
			}
		}
	}
</script>

